body {
  background-color: black;
}
img {
  width: 1000px;
  height: 550px;
  margin: 20px auto;
  padding-left: 75px;
  background-size: cover;
}
.App {     
  background: rgba(255, 255, 255, 0.2);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.25);
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
  position: relative;
  bottom: 590px;
}
#to-do-form input{
  background-color: rgb(27, 112, 137);
  border: 0;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  font-size: 18px;
  border-radius: 10px;
  color: #ffffff;
}  
#to-do-form button {
  height: 50px;
  width: 80px;
  border-radius: 10px;
  border: 0;
  background-color: rgb(202, 156, 24);
  cursor: pointer;
  font-weight: bold;
  font-size: 18px;
  color: #ffffff;
}